/* ubuntu-condensed-regular - cyrillic_latin */
@font-face {
  font-family: 'Ubuntu Condensed';
  font-style: normal;
  font-weight: 400;
  src: url(fonts/ubuntu-condensed-v10-cyrillic_latin-regular.eot); /* IE9 Compat Modes */
  src: local('Ubuntu Condensed'), local('UbuntuCondensed-Regular'),
       url(fonts/ubuntu-condensed-v10-cyrillic_latin-regular.eot?#iefix) format('embedded-opentype'), 
       url(fonts/ubuntu-condensed-v10-cyrillic_latin-regular.woff2) format('woff2'), 
       url(fonts/ubuntu-condensed-v10-cyrillic_latin-regular.woff) format('woff'), 
       url(fonts/ubuntu-condensed-v10-cyrillic_latin-regular.ttf) format('truetype'), 
       url(b35148ec48ca6c171bba16f7e2254279.svg#UbuntuCondensed) format('svg'); /* Legacy iOS */
}
